import React from "react"
import { ExternalLink, Github, Tag, User } from "lucide-react"
import { useHistory } from 'react-router-dom';


export default function ProjectCard({
  title,
  description,
  imageUrl,
  demoUrl,
  githubUrl,
  tags,
  author,
}) {

  const router = useHistory();

  return (
    <div className="group bg-white rounded-xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-300 transform hover:-translate-y-1">
      <div className="relative">
        <img src={imageUrl || "/placeholder.svg"} alt={title} className="w-full h-64 object-cover" />
        <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
      </div>
      <div className="p-6">
        <h3 className="text-2xl font-bold mb-2 text-gray-900">{title}</h3>
        <div className="flex items-center mb-3 text-gray-600">
          <User className="w-4 h-4 mr-2" />
          <span>{author}</span>
        </div>
        <p className="text-gray-600 mb-4">{description}</p>
        <div className="flex flex-wrap gap-2 mb-6">
          {tags.map((tag, index) => (
            <span key={index} className="flex items-center px-3 py-1 text-sm bg-blue-50 text-blue-600 rounded-full">
              <Tag className="w-3 h-3 mr-1" />
              {tag}
            </span>
          ))}
        </div>
        <div className="flex justify-between">
          {demoUrl && (
            <a
            href={demoUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
          >
            <ExternalLink className="w-4 h-4 mr-2" />
            Live Demo
          </a>
        )}
        {!demoUrl && (
            <button
            className="flex items-center px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            onClick={() => router.push('/demoUnavailable')}
            >
            <ExternalLink className="w-4 h-4 mr-2" />
            Live Demo
          </button>
        )}
          <a
            href={githubUrl}
            target="_blank"
            rel="noopener noreferrer"
            className="flex items-center px-4 py-2 bg-gray-800 text-white rounded-lg hover:bg-gray-900 transition-colors"
          >
            <Github className="w-4 h-4 mr-2" />
            Source Code
          </a>
        </div>
      </div>
    </div>
  )
}

